.videoCard {
    color: white;
    width: 500px;
    height: 400px;
    padding: 20px;
    transition: transform 100ms ease-in;
}

.videoCard:hover {
    transform: scale(1.09) ;
}

.videoCard > img {
    height: 300px;
    object-fit: contain;
    width: 450px;
}

.videoCard > h2 {
    padding-top: 10px;
}

.videoCard__starts {
    display: none;
}

.videoCard:hover > .videoCard__starts {
    display: inline-flex;
}