.nav {
    display: flex;
    color: white;
    overflow-x: scroll;
    padding: 10px;
    padding-left: 40px;
}

.nav::-webkit-scrollbar{
    display: none;
}

.nav > h2 {
    cursor: pointer;
    margin: 0 30px;
    font-weight: 500;
    opacity: 0.7;
    transition: transform 50ms ease-in-out;
}

.nav >h2:hover{
    opacity: 1;
    transform: scale(1.2);
}